.wrapper {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 14px 20px 0px;
  margin-bottom: 39px;
  flex-direction: row;
}

@media screen and (min-width: 768px) {
  .wrapper {
    margin-bottom: 26px;
    padding: 14px 32px 0px;
  }
}

.title {
  /* color: var(--color-white); */
  font-family: 'PoppinsMedium', sans-serif;
  font-size: 18px;
  font-style: normal;
  line-height: normal;
  letter-spacing: -0.28px;
}

@media screen and (min-width: 1440px) {
  .wrapper {
    margin-bottom: 10px;
    padding: 14px 24px 0px;
  }

  .title {
    letter-spacing: -0.36px;
  }
}

.filter {
  margin-left: auto;
  border: none;
  background-color: inherit;
  display: flex;
  gap: 8px;
  color: inherit;
  /* color: var(--color-icon-light); */
  font-family: 'PoppinsMedium', sans-serif;
  font-size: 14px;
  line-height: normal;
  letter-spacing: -0.28px;
  align-items: center;
  stroke: inherit;

  /* stroke: var(--color-icon-light); */
}

.filterText {
  height: 20px;
}

.modalFilterWrapper {
  display: block;
}

.modalFilter {
  color: inherit;
  /* color: var(--color-white); */
  font-family: 'PoppinsMedium', sans-serif;
  font-size: 18px;
  letter-spacing: -0.36px;
}

.modalBoard {
  height: 1px;
  background-color: var(--color-sidebar-border);
  margin-top: 14px;
}

.modalLabel {
  display: flex;
  justify-content: space-between;
  margin-top: 14px;
}

.modalLabelHeader {
  /* color: var(--color-white); */
  font-family: 'PoppinsMedium', sans-serif;
  font-size: 14px;
  letter-spacing: -0.28px;
}

.modalShowAll {
  border: none;
  background: none;
  color: var(--color-icon-grey);
  font-family: 'PoppinsRegular', sans-serif;
  font-size: 12px;
  letter-spacing: -0.24px;
  text-decoration-line: underline;
}

.modalCheckbox {
  display: flex;
  gap: 8px;
  color: var(--color-icon-grey);
  font-family: 'PoppinsRegular', sans-serif;
  font-size: 12px;
  letter-spacing: -0.24px;
}

.modalCheckbox:hover {
  cursor: pointer;
}

.modalListOptions {
  margin-top: 14px;
  display: inline-flex;
  flex-direction: column;
  gap: 8px;
}

.radio input {
  position: absolute;
  z-index: -1;
  opacity: 0;
  margin: 10px 0 0 7px;
}

label {
  display: flex;
  gap: 8px;
  align-items: center;
}

.radioText {
  position: relative;
  cursor: pointer;
  width: 14px;
  height: 14px;
  border-radius: 50%;
  color: var(--color-sidebar-border);
}

.radioText:before {
  content: '';
  position: absolute;
  top: -3px;
  left: 0;
  width: 14px;
  height: 14px;
  border-radius: 50%;
}

.radioText:after {
  content: '';
  position: absolute;
  top: 1px;
  left: 4px;
  width: 14px;
  height: 14px;
  border-radius: 50%;
  opacity: 0;
  transition: 0.2s;
}

.radio input:checked + .radioText:after {
  opacity: 1;
}

.radio input:focus + .radioText:before {
  width: 12px;
  height: 12px;
  border-radius: 50%;
  border: 2px solid black;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  transition: opacity 0.3s;
}

